@import url('https://fonts.googleapis.com/css?family=Pangolin');
$time: .3s;

* {
    box-sizing: border-box;
}

body {
    font-family: 'Pangolin', cursive;
}

.button {
    padding: 5px 10px;
    background-color: #ff4c4c;
    border: none;
    cursor: pointer;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    outline: none;
    transition: $time;
    font-family: inherit;
    letter-spacing: 2px;
    width: 100%;
    position: relative;
    overflow: hidden;
    height: 120px;
    &:before {
        content: "";
        position: absolute;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        background-color: rgba(#ff0000, 0.2);
        left: -20px;
        bottom: 0;
        transition: $time;
    }

    &:hover {
        color: #003666;
        &:before {
            width: 100%;
            height: 100%;
            border-radius: 0;
            left: 0;
            z-index: 0;
        }
    }
}

.quiz {
    max-width: 600px;
    margin: auto;
    padding: 15px;
    position: relative;
    overflow: hidden;
    height: 500px;

    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;

        .counter {
            position: absolute;
            left: 0;
            top: 0;
        }
        &-main {
            width: 100%;
            display: flex;
            justify-content: space-around;
            margin-bottom: 20px;
        }
        &-footer {
            font-size: 42px;
        }
    }
}
.total{
    position: absolute;
    left: 50%;
    transition: 1s;
    backface-visibility: hidden;
}
.slide-enter-active,
.slide-leave-active {
    position: absolute;
    width: calc(100% - 30px);
    transition: 1s;
    left: 15px;

}

.slide-enter,
.slide-leave-to {
    left: 100%;

}
.slide-leave-to{
    transform: rotatex(90deg);
}

.score-enter-active,
.score-leave-active {
    transform: scale(2);
}

.score-enter,
.score-leave-to {
    transform: scale(0.2);
}